import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DownloadCategoriesPageComponent } from './download-categories-page.component';

const routes: Routes = [
  { path: '', component: DownloadCategoriesPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class DownloadCategoriesPageRoutingModule { }
